import { Helmet } from "react-helmet"
import { useRecoilState } from "recoil"
import { cartData } from "../atom"
import {lazy,Suspense} from "react" 
import Wating from "../components/Wating"
const CartList = lazy(()=>import("../components/CartList"))
const Cart = () => {
    const [cartList,setCartList] = useRecoilState(cartData)
    return(<>
    <Helmet><title>cart</title></Helmet>
    <div className="mt-28 mb-7 px-7">
      {cartList.length?<div className="pb-3 flex justify-between xl:mx-28 mx-3">
      <h1 className="font-bold xl:text-2xl text-lg">List of movies to be rented</h1>
      <input type = "button" value="rental" onClick={()=>console.log(cartList)}
    className="rounded-xl font-bold text-white hover:opacity-80 p-1 bg-blue-400 w-32"/>
      </div>:null}
    <div className={`${cartList.length ?"grid grid-cols-2":"flex w-full h-80 justify-center items-center"} place-items-center`}>
    {cartList.length? cartList.map((item)=>{return(
    <Suspense key={item.id} fallback={<Wating/>}><CartList {...item} setCartList={setCartList}/></Suspense>)}):
    <h1 className="font-bold xl:text-2xl text-lg">There are no movies in the rental list...</h1>}
    </div>
    </div>
    </>)
}
export default Cart